




















import Vue from 'vue';
import Square from '@/components/Square.vue';

export default Vue.extend({
  props: {
    squares: Array,
  },
  components: {
    Square,
  },
  methods: {
    renderSquare(i: number) {
      return this.squares[i];
    },
    forwardClick(i: number) {
      this.$emit('clicked', i);
    },
  },
});
